<template>
  <div class="singer-page" ref="singerPage">
    <scroll class="singer-list"
            :data="singerGroupList"
            :probe-type="probeType"
            :is-listen-scroll="listenScroll"
            @scroll="adhereGroupTitle"
            ref="singerList"
    >

      <!--歌手列表-->
      <ul>
        <!--歌手组-->
        <li v-for="singerGroup in singerGroupList" class="group" ref="listGroup">
          <h2 class="title">{{singerGroup.title}}</h2>
          <ul>
            <!--歌手-->
            <li v-for="singer in singerGroup.items" class="item" @click="selectItem(singer)">
              <img class="avatar" v-lazy="singer.avatar" />
              <span class="name">{{singer.name}}</span>
            </li>
          </ul>
        </li>
      </ul>

      <!--侧面快速入口列表（字母列表）-->
      <div class="side-shortcut" @touchstart="startTouchShortcut" @touchmove.stop.prevent="moveTouchShortcut">
        <ul>
          <!--快速入口（字母）-->
          <li class="item each_cut"
              v-for="(shortcut,index) in shortcutList"
              :class="{'current':curGroupIndex===index}"
              :data-index="index"
          >
            {{shortcut}}
          </li>
        </ul>
      </div>

      <!--顶部固定分组名（标志当前歌手组的字母）-->
      <div class="top-fixed" ref="fixed" v-show="fixedTitle">
        <div class="fixed-title">{{fixedTitle}} </div>
      </div>

      <!--加载-->
      <div class="loading-wrapper" v-show="!singerGroupList.length">
        <loading></loading>
      </div>
    </scroll>

    <router-view></router-view>
  </div>
</template>

<script>
  import Scroll from '../../base/scroll/scroll.vue'
  import Loading from '../../base/loading/loading.vue'
  import {getSingerList} from '../../api/singer.js'
  import Singer from '../../common/js/singer.js'
  import {mapMutations} from 'vuex'
  import {playListMixin} from '../../common/js/mixin'

  export default {
    mixins: [playListMixin],
    data() {
      return {
        singerGroupList: [],//歌手组列表
        shortcutList:[],//快速入口列表（A,B,C....Z)
        scrollY: -1,//实时滚动的距离
        curGroupIndex: 0,// 当前在第几个字母
        restDis: -1//当前分组剩余可滚距离
      }
    },
    created() {
      this.receiveSingerList();
      this.touch = {};
      this.listenScroll = true;
      this.distanceList = [ ];
      this.probeType = 3;
    },
    computed: {
      //固定标题
      fixedTitle() {
        if(this.scrollY>0){//在最顶部上拉时没有固定标题
          return ''
        }else {
          return this.singerGroupList[this.curGroupIndex] ? this.singerGroupList[this.curGroupIndex].title : ''
        }
      }
    },
    methods: {
      //获得数据
      receiveSingerList() {
        getSingerList().then(res => {
          this.singerGroupList = this._normalizeSingerList(res.data.list);
          this.shortcutList = this._createShortcutList();
        })
      },

      //选择一个歌手进入歌手详情页面
      selectItem(singer) {
        console.log(singer);
        this.$router.push({
          path: `/singer-page/${singer.id}`
        });
        this.setSinger(singer);
      },

      //保留底部音乐起位置
      handlePlayList(playList) {
        const bottom = playList.length > 0 ? '60px' : '';
        this.$refs.singerPage.style.bottom = bottom;
        this.$refs.singerList.refresh()
      },

      //触摸快速入口执行一系列动作
      startTouchShortcut(e) {
        this.touch.y1 = e.touches[0].pageY;//触控起始的位置
        this.touch.startIndex = e.target.dataset.index | 0;//触控起始位于第几个字母
        let targetIndex = this.touch.startIndex;//点一下，目标位于第几个字母就是触控起始位于第几个字母
        this.toSingerGroup(targetIndex);
      },
      moveTouchShortcut(e) {
        this.touch.y2 = e.touches[0].pageY;//拖拽时的位置
        const eachH = document.getElementsByClassName("each_cut")[0].clientHeight;
        let delta = (this.touch.y2 - this.touch.y1) / eachH | 0;//向下取整得出要继续滚动几个字母
        let targetIndex = this.touch.startIndex + delta;//目标是第几个字母
        this.toSingerGroup(targetIndex);
      },
      toSingerGroup(index) {
        this.curGroupIndex = index;
        if (index < 0) {
          index = 0
        }
        if(index>this.shortcutNumbers){
          index=this.shortcutNumbers
        }
        this.$refs.singerList.scrollToElement(this.$refs.listGroup[index], 0)
      },

      // 滚动时当前分组剩余距离计算
      adhereGroupTitle(pos) {
        this.scrollY=pos.y;
      },

      //辅助处理数据
      //歌手分组有序列表的形成
      _normalizeSingerList(list) {
        //初始化歌手组列表
        let groupList = {
          hot: {//热门歌手
            title: "热门",
            items: [],
            top: 10
          },
          //姓氏A的歌手
          //~
          //姓氏Zde歌手
        };
        //按照姓氏首字母分组
        list.forEach((item, index) => {//forEach遍历数组取到当前元素、元素索引  并且传给回调函数，作为回调函数中的参数使用。
          let singer = new Singer({//item转换为singer实例
            name: item.Fsinger_name,
            id: item.Fsinger_mid
          });
          //形成字母组
          let key = item.Findex;//当前item所属于的字母
          if (!groupList[key]) {//初始化key歌手组
            groupList[key] = {
              title: key,
              items: []
            };
          }
          groupList[key].items.push(singer);//形成key歌手组
          //前十个属于热门歌手组
          if (index < groupList.hot.top) {//形成“热门”歌手组
            groupList.hot.items.push(singer)
          }
        });
        //得到有序歌手组列表（把groupList对象转换为groups数组,在对groups排序）
        let groupHot = [];
        let groupA_Z = [];
        let groupOther = [];
        for (let key in groupList) {
          let title = groupList[key].title;
          if (title.match(/[a-zA-Z]/)) {
            groupA_Z.push(groupList[key])//字母组
          }
          else if (title == "热门") {//"热门"组
            groupHot.push(groupList.hot);
          }
          else {
            groupOther.push(groupList[key])//其他组
          }
        }
        groupA_Z.sort(//字母组排序
          (last, next) => {//排序方法
            return last.title.charCodeAt(0) - next.title.charCodeAt(0)
          }
        );
        groupOther.sort();//其他组排序
        return groupHot.concat(groupA_Z).concat(groupOther)
      },
      //快速入口列表的形成
      _createShortcutList(){
        let result=this.singerGroupList.map(
          (item) => {
            let value = item.title.substring(0, 1);//截取第一个字符
            return value//每一个返回值value就是新数组result的一个元素
          }
        );
        this.shortcutNumbers=result.length-1;
        return result
      },
      //分组底部距离页面顶部的距离列表的形成
      _createDistanceList() {
        const list = this.$refs.listGroup;
        let dis = 0;
        for (let i = 0; i < list.length; i++) {
          //每个分组的距离顶端的距离记录为数组的一个元素
          dis += list[i].clientHeight;
          this.distanceList.push(dis);
        }
      },

      ...mapMutations({
        setSinger: 'SET_SINGER'
      })
    },
    watch: {
      singerGroupList() {
        setTimeout(() => {
          this._createDistanceList();
        }, 20)
      },
      // 固定标题栏 被 分组块 逐渐顶上去的效果
      scrollY(newv) {
        let scroll_y = Math.abs(newv);
        let res_dis; //当前分组剩余可滚距离
        //超过上边界
        if (newv >= 0) {
          this.curGroupIndex = 0;
          res_dis=res_dis = this.distanceList[0]
        }
        //超过下边界
        else if (scroll_y >= this.distanceList[this.distanceList.length - 1]) {
          this.curGroupIndex = this.distanceList.length - 1;
          res_dis = 0;
        }
        // 第一块
        else if (scroll_y >= 0 && scroll_y < this.distanceList[0]) {
          this.curGroupIndex = 0;
          res_dis = this.distanceList[0] - scroll_y;//第一块还剩多少距离没有滚过
        }
        // 中间
        else {
          for (let i = 0; i < this.distanceList.length; i++) {
            if (scroll_y >= this.distanceList[i] && scroll_y < this.distanceList[i + 1]) {
              this.curGroupIndex = i + 1;
              res_dis = this.distanceList[i + 1] - scroll_y;//当前块还剩多少没有滚过
              break;
            }
          }
        }
        this.restDis=res_dis;
        const ___FBH___ = 30 ;//固定标题栏高度
        let trans_y;//固定标题栏 偏移的值
        if(res_dis > 0 && res_dis < ___FBH___){//当前分组 剩余可滚距离 在 0-固定标题栏高度 之间时候，也就是分组标题和固定标题栏重合时
           trans_y = -(___FBH___-res_dis);//发生偏移，已经重合的距离 的 负数 就是固定标题栏translatey的值
        }
        else {//非重合时
           trans_y = 0//不偏移
        }
        this.$refs.fixed.style.transform = `translate3d(0,${trans_y}px,0)`//移动
      }
    },
    components: {
      Scroll,
      Loading
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .singer-page
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
    .singer-list
      position: relative
      width: 100%
      height: 100%
      overflow: hidden
      background: rgba(230, 198, 209, 0.33);
      .group
        padding-bottom: 30px
      .title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
      .item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-l
          font-size: $font-size-medium
      .side-shortcut
        position: absolute
        z-index: 30
        right: 0
        top: 50%
        transform: translateY(-50%)
        width: 20px
        padding: 20px 0
        border-radius: 10px
        text-align: center
        background: $color-background-d
        font-family: Helvetica
        .item
          padding: 3px
          line-height: 1
          color: $color-text-l
          font-size: $font-size-small
          &.current
            color: $color-theme
      .top-fixed
        position: absolute
        top: 0
        left: 0
        width: 100%
        .fixed-title
          height: 30px
          line-height: 30px
          padding-left: 20px
          font-size: $font-size-small
          color: $color-text-l
          background: $color-highlight-background
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)

</style>



